@import '../../index.scss';

.auth {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--body);
    opacity: 0.3;
    content: '';
  }
}

.auth__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 10px;
  padding: 50px;
  border-radius: 20px;
  background-color: var(--bgc-tertiary);
  box-shadow: 0 0 7px 3px var(--color-secondary-3);
  border: 1px solid var(--main);
}

@media screen and (min-width: 375px) {
  .auth__wrapper {
    padding: 50px 60px 60px 60px;
  }
}

@media screen and (min-width: 425px) {
  .auth__wrapper {
    width: 400px;
    padding: 60px 70px 70px 70px;
  }
}

.auth__theme-switcher {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.auth__heading {
  font-size: 26px;
  margin: 0;
  text-align: center;
}

.auth__text {
  text-align: center;
  font-size: 14px;
}

.auth__button {
  padding: 10px 0;
}
